import React from 'react';
import styled from 'styled-components'
import { createForm, formShape } from 'rc-form';
import Card from '../../stories/organisms/Card';
import Button from '../../stories/atoms/Button';
import List from '../../stories/atoms/List';

const CartArray = [
    {
        id: 1,
        img: 'assets/img/icon_store.svg',
        title: '超商取貨 付款',
        content: '24隔日取貨說明'
    },
    {
        id: 2,
        img: 'assets/img/icon_card.svg',
        title: '信用卡 付款',
        content: 'VISA, Master, JCB, 聯合信用卡'
    },
    {
        id: 3,
        img: 'assets/img/line_pay.svg',
        title: 'LINE Pay 付款',
        content: '使用line point折抵消費'
    },
    {
        id: 4,
        img: 'assets/img/unionpay.png',
        title: '銀聯卡 付款',
        content: '支付成功頁面僅為銀聯卡回覆訊息，交易是否完成請需以本商店通知為準'
    },
    {
        id: 5,
        img: 'assets/img/icon_atm.svg',
        title: 'Web ATM 付款',
        content: '網路銀行ATM操作說明'
    }
]

const ListContent = `<li>・請確認您填寫的資料是否正確，一旦訂單完成後，付款與物流方式皆無法修改。</li>
<li>・使用ATM轉帳，將依據銀行入帳日，依序進行出貨/排貨。</li>
<li>・每筆訂單所提供的「ATM專屬虛擬帳號」皆不同，若您欲使用本項服務，敬請放心進行匯款作業，安全又便利！</li>
<li>・若訂單內含預購、無庫存商品調貨時間請參考「商品平均調貨時間」。</li>
<li>・若您對取貨或付款的方式有疑問，請詳閱<a href="www">「購買說明」</a>。</li>
<li style="color: #000000">・請確認您已詳閱並瞭解本站<a href="www">「購買說明」</a>內容，訂單完成即表示您已同意其中的各項說明。</li>`

type Props = {
    form: formShape
};

type State = {
    yourRadio: string
};

class Form extends React.PureComponent<Props, State> {
    state = {
        yourRadio: ''
    }

    handleRadio = (e) => {
        this.setState({
            yourRadio: e.target.value
        })
    }

    onSubmit = (e) => {
        // e.preventDefault();
        this.props.form.validateFields((error, value) => {
        console.log(error, value);
        });
    }

    render() {
        const {
            form: {getFieldDecorator, getFieldValue, setFieldsValue}
            } = this.props;
        
        const {yourRadio} = this.state;
        return(
            <Main action="" style={{maxWidth: '825px', width: '95%'}}>
                <CardContainer>
                    {getFieldDecorator('depositBankId', {
                            validateFirst: true,
                            rules: [{
                                required: true
                            }]
                        })(
                            <Card source={CartArray} onChange={this.handleRadio} yourRadio={yourRadio}/>
                        )}
                </CardContainer>
                <ListContainer>
                    <ListBlack ListContent={ListContent}/>
                    <ListImg src="assets/img/icon_notice.svg" alt=""/>
                </ListContainer>
                <ButtonContainer>
                    <Button>上一步</Button>
                    <Button theme="primary" type="reset" onClick={this.onSubmit}>下一步</Button>
                </ButtonContainer>
            </Main>
        )
    }
};

export default createForm()(Form);

const Main = styled.form`
    margin: 55px auto 30px;
`;

const CardContainer = styled.ul`
    display: flex;
    flex-wrap: wrap;
    margin: 0 -25px 40px -25px;
`;

const ButtonContainer = styled.div`
    display: flex;
    justify-content: flex-end;
`;

const ListBlack = styled(List)`
    margin: 40px 0 15px;
`;

const ListContainer = styled.div`
    position: relative;
    margin-bottom: 60px;
`;

const ListImg = styled.img`
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
`;