import * as React from 'react';
import styled from 'styled-components';

type Props = {
    style?: React.CSSProperties,
    className?: string,
    children?: React.Node,
    theme?: 'default' | 'primary',
    type?: 'button' | 'submit'
};

type State = {};

const themeConfig = {
    default: {
        bgColor: 'transparent',
        fontColor: '#5A5E66',
        borderColor: '#DCDFE6'
    },
    primary: {
        bgColor: '#4BC9C9',
        fontColor: '#FFFFFF',
        borderColor: '#4BC9C9'
    }
};

class Button extends React.PureComponent<Props, State> {
    static defaultProps = {
        theme: 'default',
        type: 'button'
    };

    render() {
        const {style, className, children, theme, ...buttonProps} = this.props

        const themeProps = theme ? themeConfig[theme] : {};

        return(
            <ButtonRoot {...buttonProps} style={style} className={className} config={{...themeProps}}>{children}</ButtonRoot>
        )
    }
}

export default Button;

const ButtonRoot = styled.button`
    border: 1px solid ${props => props.config.borderColor && props.config.borderColor};
    border-radius: 4px;
    min-width: 155px;
    min-height: 55px;
    background-color: ${props => props.config.bgColor && props.config.bgColor};
    color: ${props => props.config.fontColor && props.config.fontColor};
    cursor: pointer;
    font-family: Arial, Helvetica, '微軟正黑體', sans-serif;
    font-size: 20px;

    &:not(:last-of-type){
        margin-right: 20px;
    }
`;